.counter{
  width: 100%;
  height: 100vh;
  background-color: #38535F;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.inp-btn{
  width: 360px;
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  background-color: #1D3A48;
  box-shadow: 1px 1px 3px black;
}
input{
  background-color: #1D3A48;
  border: none;
  color: #FFFFFF;
  border-bottom: 1px solid #FFFFFF;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
input::placeholder{
  color: #FFFFFF;
}
.search{
  width: 90px;
  height: 20px;
  border-radius: 20px;
  border: none;
  box-shadow: 1px 1px 3px black;
  background-color: #FFFFFF;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.about{
  width: 1090px;
  height: 550px;
  box-shadow: 1px 1px 3px black;
  background-color: #F3F8F9;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.name{
  width: 1050px;
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.m-name{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color: #516A7D;
}
h2{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
   color: #516A7D;
}
h3{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color: #516A7D;
}
h4{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color: #516A7D;
}
.m-imbd{
  width: 100px;
  height: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #FFFFFF;
  background-color: #C89E1D;
  box-shadow: 1px 1px 2px black;
  border-radius: 5px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.things{
  width: 1090px;
  height:460px ;
  display: flex;
  flex-direction: row;
}
.left{
  width: 400px;
  height: 460px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.right{
  width: 690px;
  height: 460px;
  padding-left: 10px;
}
.plot{
  background-color: #EAF0F1;
  padding: 5px;
  border-radius: 5px;
}
.blue{
  background-color: #E4F0F6;
}